@mixin adaptiveValue($property, $startSize, $minSize) {
  $addSize: $startSize - $minSize;
  #{$property}: $startSize + px;
  @media (max-width: #{$maxWidthContainer + px}) {
    #{$property}: calc(
      #{$minSize + px} + #{$addSize} *
        ((100vw - #{$minWidth + px}) / #{$maxWidthContainer - $minWidth})
    );
  }
}
